	refreshTop = () => {
		this.setState({fetching: true})
		this.change2(this.state.activeTab)
		setTimeout(() => {
			this.setState({fetching: false})
		}, 3000);
	}
	onRefresh = () => {
		this.setState({ fetching: true });
		//this.setState({popout: <ScreenSpinner />})
		fetch("https://ivandanilov.pythonanywhere.com/admins")
		.then(response => response.json())
		.then(data => {
			var chat_arr = data.items.map((chat, i) => {
				if(chat.user_id === this.state.fetchedUser.id){
					return {chat_id: chat.chat_id, lvl: chat.level}
				}
			})
			chat_arr = chat_arr.filter(function (el) {
				return el != null;
			});
			if(chat_arr.length === 0){
				this.setState({
					popout: null,
					auth: true
				})
			}else{
				fetch("https://ivandanilov.pythonanywhere.com/chats")
				.then(response => response.json())
				.then(data => {
					var chats = [];
					for(var i = 0; i < chat_arr.length; i++){
						for(var j = 0; j < data.items.length; j++){
							if(data.items[j].chat_id === chat_arr[i].chat_id){
								var obj = data.items[j];
								obj.level = chat_arr[i].lvl;
								chats.push(obj)
							}
						}
						if(i === chat_arr.length-1){
							this.setState({
								popout: null,
								chats: chats,
								auth: true
							})
						}
					}
				})	
			}
		});
		setTimeout(() => {
			this.setState({fetching: false})
		}, 1500);
	}
	onChange (search) { this.setState({ search }); } 
	openAdm() {
		fetch('https://ivandanilov.pythonanywhere.com/reports')
		.then(res => res.json())
		.then(res => {
			var chats = []
			for(var i = 0; i < res.items.length; i++) {
				if(res.items[i].otvet === '') {
				var obj = res.items[i];
				chats.push(obj) }
			}
			this.setState({reports: chats})
		})
		this.setState({activePanel: 'adm', activeStory: 'adm', activeTab: 'nototv'})
	}
	get currentUsers () {
		const search = this.state.search.toLowerCase();
		if (this.state.currentUsers === undefined) {	
			return;
		}
		return this.state.currentUsers.filter(({first_name, last_name}) => (first_name+" "+last_name).toLowerCase().indexOf(search) > -1);
	}
	
	openBanList(chat) {
		this.setState({banlist: [], banlists: []})
		fetch('https://ivandanilov.pythonanywhere.com/banlist/'+chat.chat_id)
		.then(res => res.json())
		.then(res => {
			this.setState({banlists: res.response.split(',')});
			connect.send("VKWebAppCallAPIMethod", {"method": "users.get", "params": {"user_ids": res.response,
		"fields": "photo_100", "v":"5.95", "access_token":"d30b89a1b479a41d3d512fb91e54d515d21d1fba2382d5fcf7aacfe2225e8d3a462fe3381e2704ba58340"}});
		});
		this.setState({
			activePanel: 'banlist',
			activeStory: 'banlist',
			currentChat: chat,
			popout: <ScreenSpinner />
		})
		this.setState({popout: ''})
	}

	openChat(e, chat){
		this.setState({
			activePanel: 'currentChat',
			currentChat: chat,
			indexChat: e,
			popout: <ScreenSpinner />
		})
		fetch('https://ivandanilov.pythonanywhere.com/chat_users/'+chat.chat_id)
		.then(response => response.json())
		.then(data => {
			if (data.error === "[917] You don't have access to this chat") {
				this.setState({activePanel: 'home', activeStory: 'manage'});
				delete this.state.chats[e];
				this.setState({ popout: 
					<Alert 
					actions={[{ 
					title: 'Закрыть', 
					autoclose: true, 
					style: 'cancel' 
					}]} 
					onClose={this.closePopout} 
					> 
					Произошла ошибка. Бот был ранее исключен из беседы
					</Alert> 
					});
					fetch('https://ivandanilov.pythonanywhere.com/chats/'+chat.chat_id+'/delchat');
					return
			}
			console.log(data)
			this.setState({popout: null, currentUsers: data.items})
			this.check_akick(chat.chat_id)
		})
	}

