		change = (e) => { this.change2(e.currentTarget.id);}
	change2(id) {
		switch(id){
			case 'all':
				fetch('https://ivandanilov.pythonanywhere.com/reports')
					.then(res => res.json())
					.then(res => {
					var chats = []
					for(var i = 0; i < res.items.length; i++) {
						var obj = res.items[i];
						chats.push(obj)
					}
					this.setState({reports: chats.reverse()})
				})
				this.setState({activePanel: 'adm', activeStory: 'adm', activeTab: 'all', editrep: false})
				break;
			case 'nototv':
				fetch('https://ivandanilov.pythonanywhere.com/reports')
				.then(res => res.json())
				.then(res => {
				var chats = []
				for(var i = 0; i < res.items.length; i++) {
					if (res.items[i].otext === '') {
					var obj = res.items[i];
					chats.push(obj) }
				}
				this.setState({reports: chats})
				})
				this.setState({activeTab: 'nototv', editrep: false})
				break;
			case 'otv':
				fetch('https://ivandanilov.pythonanywhere.com/reports')
				.then(res => res.json())
				.then(res => {
				var chats = []
				for(var i = 0; i < res.items.length; i++) {
					if (res.items[i].otext !== '') {
					var obj = res.items[i];
					chats.push(obj) }
				}
				this.setState({reports: chats.reverse(), activeTab: 'otv', editrep: false}) })
				break;
			case 'today':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_today - b.message_today)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_today; return tmp;}),
					activeTab: "today",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_today : 0
				})
				break
			case 'week':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_week - b.message_week)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_week; return tmp;}),
					activeTab: "week",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_week : 0
				})
				break;
			case 'month':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_month - b.message_month)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_month; return tmp;}),
					activeTab: "month",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_month : 0
				})
				break;
			case 'year':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_year - b.message_year)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_year; return tmp;}),
					activeTab: "year",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.user_id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_year : 0
				})
				break;	
			default:
		}
	}

	unban(chat_id, user_id, uindex = null) {
		fetch("https://ivandanilov.pythonanywhere.com/chats/"+chat_id+"/unban?token=4b85ea42-4768-4bce-af88-d5673d6e2973&user_ids="+user_id+"&from_id="+this.state.fetchedUser.id)
			.then(res => res.json())
			.then(res => {
				if (res.items.find((e) => e.error)) {
					this.setState({ popout: 
						<Alert 
						actions={[{ 
						title: 'Закрыть', 
						autoclose: true, 
						style: 'cancel' 
						}]} 
						onClose={this.closePopout} 
						> 
						Недостаточно прав, чтобы разблокировать пользователя
						</Alert> 
						});
					return
				}
			if (uindex !== null) {
				let users = this.state.banlists;
				users.splice(uindex, 1);
				this.setState({
					banlists: users
				});
				this.setState({ popout: 
					<Alert 
					actions={[{ 
					title: 'Закрыть', 
					autoclose: true, 
					style: 'cancel' 
					}]} 
					onClose={this.closePopout} 
					> 
					Пользователь был разблокирован
					</Alert> 
					})
					try {
					document.getElementById('a_'+uindex).style.display = "none" }
					catch(e) {}
			}
		})
	}

