	setTitle(chat) {
		if (this.state.title === "") {
			this.setState({ popout: 
				<Alert 
				actions={[{ 
				title: 'Закрыть', 
				autoclose: true, 
				style: 'cancel' 
				}]} 
				onClose={this.closePopout} 
				> 
				Укажите новое название для беседы
				</Alert> 
				})
				return
		}
		connect.send("VKWebAppCallAPIMethod", {"method": "messages.editChat", "params": {"chat_id": this.state.currentChat.chat_id,
		title: this.state.title, "v":"5.95", "access_token":"d30b89a1b479a41d3d512fb91e54d515d21d1fba2382d5fcf7aacfe2225e8d3a462fe3381e2704ba58340"}})
		this.setState({activePanel: "currentChat", activeStory: 'manage'})
		this.setState({ popout: 
			<Alert 
			actions={[{ 
			title: 'Закрыть', 
			autoclose: true, 
			style: 'cancel' 
			}]} 
			onClose={this.closePopout} 
			> 
			Название беседы было изменено на <b>{this.state.title}</b>
			</Alert> 
			})
			chat.title = this.state.title
			this.setState({title: ''})
			fetch('https://ivandanilov.pythonanywhere.com/chats/'+this.state.currentChat.chat_id+'/check_update')
	}
